import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ViewController, ModalController, Loading } from 'ionic-angular'; 
import { LocaldataProvider } from "../../providers/localdata/localdata";

@IonicPage({
  name: 'selectanfang',
  segment: 'selectanfang'
})
@Component({
    selector: 'select-anfang',
    templateUrl: 'selectanfang.html'
})
export class SelectanfangComponent {
    loading: Loading;
    isMultiple:boolean = false;
    isWaichu:boolean = false;
    isChaifang:boolean = false;
    isZaijia:boolean = false;
    scenes:Array<string> = [];
    scene:string = "";
    //  <!--<ion-select multiple="true" placeholder="请至少选择一种安防模式" interface="action-sheet" okText="确定" cancelText="取消" [(ngModel)]="autojobInfo.scenes">
    //                     <ion-option value="外出设防">外出设防模式下执行</ion-option>
    //                     <ion-option value="拆防">拆防模式下执行</ion-option>
    //                     <ion-option value="在家设防">在家设防模式下执行</ion-option>
    //                 </ion-select>-->
    constructor(public navCtrl: NavController,
        public modalCtrl: ModalController,
        public params: NavParams,
        private viewCtrl: ViewController
        , private localDataService:LocaldataProvider
    ) {
        
        let model = params.get("item");
        if(model != null && model.isMultiple) {
            this.isMultiple = model.isMultiple;
        }
        if(model != null && model.scene != null && model.scene != "") {
            this.scene = model.scene;
        }
        if(this.isMultiple) {
            if(model != null && model.scenes) {
                this.isMultiple = model.isMultiple;
                this.scenes = model.scenes;
                this.initScenes();
            }
        } else {

        }
    }
    
    initScenes() {
        this.scenes.forEach(item=> {
            if(item == "外出设防") {
                this.isWaichu = true;
            } else if(item == "拆防") {
                this.isChaifang = true;
            } else if(item == "在家设防") {
                this.isZaijia = true;
            }
            

        });
    }

    selectScene(scene:string) {
        this.scene = scene;
        this.dismiss(this.scene);
    }
    

    ok() {
        if(this.isWaichu || this.isChaifang || this.isZaijia) {
            this.scenes = [];
            if(this.isWaichu) {
                this.scenes.push("外出设防");
            }
            if(this.isChaifang) {
                this.scenes.push("拆防");
            }
            if(this.isZaijia) {
                this.scenes.push("在家设防");
            }
            this.dismiss(this.scenes);
        } else {
             this.localDataService.alert("提示", "请至少选择一种安防模式。");
        }
        
    }

    dismiss(data:any) {
        this.viewCtrl.dismiss(data);

    }
}


